// ==============================
// Custom style
// 自定义样式
// ==============================
html {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, PingFang SC, Microsoft YaHei UI, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-display: swap;
  font-size: 16px;
  line-height: 1.5rem;
  width: 100%;
}
::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
::-webkit-scrollbar-thumb {
  background-color: #87878d;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #a9a9b3;
}
::selection {
  background-color: rgba(53, 166, 247, 0.25);
}
[theme="dark"] ::selection {
  background-color: rgba(50, 112, 194, 0.4);
}
body {
  background-color: #fff;
  color: #161209;
  word-wrap: break-word;
  overflow-wrap: break-word;
  scrollbar-color: auto;
}
body[theme="dark"] {
  color: #a9a9b3;
  background-color: #292a2d;
}
input::-ms-clear {
  display: none;
}
a,
a::before,
a::after {
  text-decoration: none;
  color: #161209;
}
[theme="dark"] a,
[theme="dark"] a::before,
[theme="dark"] a::after {
  color: #a9a9b3;
}
a:active,
a:hover {
  color: #2d96bd;
}
[theme="dark"] a:active,
[theme="dark"] a:hover {
  color: #fff;
}
#mask {
  background-repeat: no-repeat;
  background-position: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0);
}
.blur #mask {
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.25);
}
svg.icon {
  display: inline-block;
  width: 1.25em;
  height: 1em;
  text-align: center;
}
svg.icon path {
  fill: currentColor;
}
img.emoji {
  height: 1em;
  width: 1em;
  margin: 0 0.05em 0 0.1em;
  vertical-align: -0.1em;
}
.details .details-summary:hover {
  cursor: pointer;
}
.details i.details-icon {
  color: #a9a9b3;
  -webkit-transition: transform 0.2s ease;
  -moz-transition: transform 0.2s ease;
  -o-transition: transform 0.2s ease;
  transition: transform 0.2s ease;
}
[theme="dark"] .details i.details-icon {
  color: #5d5d5f;
}
.details .details-content {
  max-height: 0;
  overflow-y: hidden;
  -webkit-transition: max-height 0.8s cubic-bezier(0, 1, 0, 1) -0.1s;
  -moz-transition: max-height 0.8s cubic-bezier(0, 1, 0, 1) -0.1s;
  -o-transition: max-height 0.8s cubic-bezier(0, 1, 0, 1) -0.1s;
  transition: max-height 0.8s cubic-bezier(0, 1, 0, 1) -0.1s;
}
.details.open i.details-icon {
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
}
.details.open .details-content {
  max-height: 12000px;
  -webkit-transition: max-height 0.8s cubic-bezier(0.5, 0, 1, 0) 0s;
  -moz-transition: max-height 0.8s cubic-bezier(0.5, 0, 1, 0) 0s;
  -o-transition: max-height 0.8s cubic-bezier(0.5, 0, 1, 0) 0s;
  transition: max-height 0.8s cubic-bezier(0.5, 0, 1, 0) 0s;
}
#fixed-buttons {
  display: none;
}
.fixed-button {
  display: none;
  z-index: 100;
  position: fixed;
  right: 1.5rem;
  font-size: 1rem;
  line-height: 1.3rem;
  padding: 0.6rem 0.6rem;
  color: #a9a9b3;
  background: #f8f8f8;
  -webkit-border-radius: 2rem;
  -moz-border-radius: 2rem;
  border-radius: 2rem;
  -webkit-transition: color 0.4s ease;
  -moz-transition: color 0.4s ease;
  -o-transition: color 0.4s ease;
  transition: color 0.4s ease;
}
.blur .fixed-button {
  -webkit-filter: blur(1.5px);
  -moz-filter: blur(1.5px);
  -ms-filter: blur(1.5px);
  filter: blur(1.5px);
}
.fixed-button:hover,
.fixed-button:active {
  color: #161209;
  cursor: pointer;
}
.fixed-button:active,
.fixed-button:focus,
.fixed-button:hover {
  outline: none;
}
[theme="dark"] .fixed-button {
  color: #5d5d5f;
  background: #252627;
}
[theme="dark"] .fixed-button:hover,
[theme="dark"] .fixed-button:active {
  color: #a9a9b3;
}
#back-to-top {
  display: block;
  bottom: 1.5rem;
}
#view-comments {
  bottom: 4.5rem;
}
.cc-window.cc-banner .cc-btn {
  color: #161209;
}
.cc-window.cc-banner .cc-btn:hover,
.cc-window.cc-banner .cc-btn:focus {
  background-color: #ccc;
}
[theme="dark"] .cc-window.cc-banner .cc-btn {
  color: #161209;
}
[theme="dark"] .cc-window.cc-banner .cc-btn:hover,
[theme="dark"] .cc-window.cc-banner .cc-btn:focus {
  background-color: #fff;
}
img {
  -o-object-fit: contain;
  object-fit: contain;
  font-family: "object-fit: contain;";
}
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1 0 auto;
}
.wrapper main .container {
  padding: 0 1rem;
}
.page {
  position: center;
  // position: relative;
  max-width: 2000px;
  width: 95%;
  margin-left:10px;
  margin-right:0px; 
  // padding-right: 0px;
  // margin: 0 auto;
}
#articleAll{
  position: relative;
  // max-width: 800px;
  max-width: 1500px;
  // width: 60%;
  width: 80%;
  margin-left:0px;
  margin-right:0px;
  // margin: 0 auto;
}
[header-desktop] .page {
  padding-top: 3.5rem;
}
[header-desktop="normal"] .page {
  padding-top: 0;
}
.blur .page {
  -webkit-filter: blur(1.5px);
  -moz-filter: blur(1.5px);
  -ms-filter: blur(1.5px);
  filter: blur(1.5px);
}
.toc .toc-title {
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
}
.toc .toc-content {
  font-size: 1rem;
}
.toc .toc-content ul {
  text-indent: -0.85rem;
  padding-left: 0.8rem;
  list-style: none;
}
.toc .toc-content ul a:first-child::before {
  content: "|";
  font-weight: bolder;
  margin-right: 0.5rem;
  color: #2d96bd;
}
[theme="dark"] .toc .toc-content ul a:first-child::before {
  color: #55bde2;
}
.toc .toc-content ul ul {
  padding-left: 1.5rem;
}
.toc ruby {
  background: #f5f5f5;
}
.toc ruby rt {
  color: #a9a9b3;
}
[theme="dark"] .toc ruby {
  background: #272c34;
}
[theme="dark"] .toc ruby rt {
  color: #5d5d5f;
}
#toc-auto {
  display: block;
  position: absolute;
  // position: fixed;
  width: 12000px;
  max-width: 400px;
  // max-width: 511px;
  padding: 0 0.8rem;
  border-left: 4px solid #f0f0f0;
  word-wrap: break-word;
  overflow-wrap: break-word;
  box-sizing: border-box;
  top: 10rem;
  // top: 104px;
  // left: 1399px;
  left: auto;
  right: 0;
  margin-right: 0px;
  // visibility: visible;
  visibility: hidden;;
}
[header-desktop="normal"] #toc-auto {
  top: 5rem;
}
.blur #toc-auto {
  -webkit-filter: blur(1.5px);
  -moz-filter: blur(1.5px);
  -ms-filter: blur(1.5px);
  filter: blur(1.5px);
}
[theme="dark"] #toc-auto {
  border-left-color: #363636;
}
#toc-auto .toc-title {
  margin: 0.8rem 0;
}
#toc-auto .toc-content.always-active ul {
  display: block;
}
#toc-auto .toc-content > nav > ul {
  margin: 0.625rem 0;
}
#toc-auto .toc-content ul ul {
  display: none;
}
#toc-auto .toc-content ul .has-active > ul {
  display: block;
}
#toc-auto .toc-content a.active {
  font-weight: bold;
  color: #2d96bd;
}
[theme="dark"] #toc-auto .toc-content a.active {
  color: #55bde2;
}
#toc-auto .toc-content a.active::before {
  color: #ef3982;
}
[theme="dark"] #toc-auto .toc-content a.active::before {
  color: #bdebfc;
}
#toc-static {
  display: none;
  margin: 0.8rem 0;
}
#toc-static[kept="true"] {
  display: block;
}
#toc-static .toc-title {
  display: flex;
  justify-content: space-between;
  line-height: 2em;
  padding: 0 0.75rem;
  background: #e6e6e6;
}
[theme="dark"] #toc-static .toc-title {
  background: #1a1d23;
}
#toc-static .toc-content {
  background-color: #f5f5f5;
}
#toc-static .toc-content > nav > ul {
  margin: 0;
  padding: 0.4rem 1rem 0.4rem 1.8rem;
}
[theme="dark"] #toc-static .toc-content {
  background-color: #272c34;
}
#toc-static.open .toc-title {
  background: #ededed;
}
[theme="dark"] #toc-static.open .toc-title {
  background: #20252b;
}
.friend.url {
  text-decoration: none !important;
  color: black;
}
.friend.logo {
  width: 56px !important;
  height: 56px !important;
  border-radius: 50%;
  border: 1px solid #ddd;
  padding: 2px;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  margin-top: 14px !important;
  margin-left: 14px !important;
  background-color: #fff;
}
.friend.block.whole {
  height: 92px;
  margin-top: 10px;
  width: 48%;
  display: inline-block !important;
  border-radius: 5px;
  background: rgba(212, 235, 235, 0.2);
  box-shadow: 4px 4px 2px 1px rgba(0, 0, 255, 0.2);
}
.friend.block.whole:hover {
  background: rgba(87, 142, 224, 0.15);
}
.friend.block.whole:hover .friend.block.left img {
  transition: 0.9s !important;
  -webkit-transition: 0.9s !important;
  -moz-transition: 0.9s !important;
  -o-transition: 0.9s !important;
  -ms-transition: 0.9s !important;
  transform: rotate(360deg) !important;
  -webkit-transform: rotate(360deg) !important;
  -moz-transform: rotate(360deg) !important;
  -o-transform: rotate(360deg) !important;
  -ms-transform: rotate(360deg) !important;
}
.friend.block.left {
  width: 92px;
  float: left;
}
.friend.block.left {
  margin-right: 2px;
}
.friend.block.right {
  margin-top: 18px;
  margin-right: 18px;
}
.friend.name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.friend.info {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
@media screen and (max-width: 600px) {
  .friend.info {
    display: none;
  }
  .friend.block.left {
    width: 84px;
    margin: auto;
  }
  .friend.block.right {
    height: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .friend.name {
    font-size: 14px;
  }
}
