.pagination {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style: none;
  white-space: nowrap;
  width: 100%;
  padding: 1rem 0 0;
}
.pagination a {
  font-size: 0.8rem;
  color: #bfbfbf;
  letter-spacing: 0.1rem;
  font-weight: 700;
  padding: 5px 5px;
  text-decoration: none;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.pagination li {
  padding-bottom: 3px;
  margin: 0 20px;
  box-sizing: border-box;
  position: relative;
  display: inline;
}
.pagination li.disabled {
  display: none;
}
.pagination li:hover a {
  color: #000;
}
[theme="dark"] .pagination li:hover a {
  color: #fff;
}
.pagination li:before,
.pagination li:after {
  position: absolute;
  content: "";
  width: 0;
  height: 3px;
  background: #000;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  bottom: 0px;
}
[theme="dark"] .pagination li:before,
[theme="dark"] .pagination li:after {
  background: #fff;
}
.pagination li:before .active,
.pagination li:after .active {
  width: 100%;
}
.pagination li:before {
  left: 50%;
}
.pagination li:after {
  right: 50%;
}
.pagination li:hover:before,
.pagination li:hover:after {
  width: 50%;
}
.pagination li.active a {
  color: #000;
}
[theme="dark"] .pagination li.active a {
  color: #fff;
}
.pagination li.active:before,
.pagination li.active:after {
  width: 60%;
